.label {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  color: #3e3e3e;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 99px;
  height: 35px;
  position: absolute;
  top: 40px;
  left: 0;
  background: #fed74b;
}

@media screen and (min-width: 1000px) {
  .label {
      font-size: 16px;
      width: 154px;
      height: 42px;
  }
}